import React, { Component } from 'react';
import './App.css';

export default class App extends Component {

  constructor() {
    super();
    this.state = {
      // movie_id: jsondata.id,
      Data: [],
    };
  }

  componentDidMount() {
    this.LoadData();
  }

  LoadData = () => {

    // url = 'https://api.themoviedb.org/3/movie/popular?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=1' ;

    const key = '4e44d9029b1270a757cddc766a1bcb63';
    const Default_URL = 'https://api.themoviedb.org/3/movie/popular?api_key=' + key + '&language=en-US&page=1';


    fetch(Default_URL)
      .then(function (response) {
        return response.json();
      })

      .then(function (json) {
        let array = json.results.map((jsondata) => {
          var Picture_Path = 'https://image.tmdb.org/t/p/w500' + jsondata.poster_path;
          var title = jsondata.title;
          var overview = jsondata.overview;
          var release_date = jsondata.release_date;
          var vote = jsondata.vote_average;
          var popularity = jsondata.popularity / 100;

          return (
            <div className="Details">
              <img className="pictures" src={Picture_Path}></img>

              <div className="heading">
                <h2>{title}</h2>
                <span>{vote}</span>
              </div>

              <h3>Release Date: {release_date}</h3>
              <p>Popularity: {popularity} %</p>

              <p>{overview}</p>
            </div>
          )
        })

        this.setState({ Data: array });
      }.bind(this))
  }



  render() {

    return (

      <div className="App">

        <header className="App-Header">

          <h1>Popular Movies</h1>

        </header>

        <div className="Data">
          {this.state.Data}
        </div>

      </div>

    );
  }
}